/* Main */

header {
    .logo-wrapper {
        max-width: 120px;
        img {
            max-width: 75%;
            margin: 5px 10px 5px 0;
        }
    }
    .nav-scroller {
        background-color: $black;
    }

    .header-top {
        font-size: $font-size-16;
        .btn {
            padding: .15rem .75rem;
            font-size: $font-size-18;
            font-weight: 700;
            line-height: 2.1;
            &.btn-login {
                color: $white;
                &:hover {
                    background-color: $ddyellow;
                    border-color: $ddyellow;
                }
            }
            &.btn-signup {
                &:hover {
                    background-color: $color-green;
                    border-color: $ddyellow;
                }
            }
        }
        .nav-link {
            &:hover {
                text-decoration: underline;
            }
        }
    }
    .header-highlight {

    }
    .header-navs {

    }

    .btn-login {

    }
}

.txt-welcome {
    font-weight: 500;
}

.dropdown-account {
    li {
        i {
            margin-right: 5px;
        }
    }
}

.nav-pills {
    .nav-link {
        &.active {
            background-color: $dyellow;
        }
    }
}
.nav-link {
    color: $dyellow;
    &:hover {
        color: $yellow;
    }
}

.logo-title {
    font-family: $font-noto;
    font-size: $font-size-24;
    color: $yellow;
}

.carousel-banner {
    .carousel-item {
        height: 400px;
    }
    .banner-bg {
        width: 100%;
    }
}

.section-header {
    position: relative;
    padding: 50px 10px 5px 10px;
    margin-bottom: 20px;
    border-bottom: solid 1px $yellow;
    h4, h5 {
        font-family: $font-noto;
        color: $dyellow;
    }
    &:before {
        @include centered-horizontal();
        bottom: 0;
        width: 200px;
        height: 3px;
        background-color: $yellow;
        content: "";
    }
    &.left-aligned {
        text-align: left;
        &:before {
            position: absolute;
            left: 0;
            bottom: 0;
            transform: none;
            width: 200px;
            height: 3px;
            background-color: $yellow;
            content: "";
        }
    }
}

.product-listings-wrapper { 
    
    select {
        border-radius: 20px;
        outline: none;
        border: 1px solid #ddd;
        color: #555
    }
    
    img {
        object-fit: contain;
        width: 150px;
        height: 200px;
        display: flex;
        align-items: center;
        justify-content: center
    }
    
    .product-name {
        padding: 5px 8px;
        margin-bottom: 0;
        font-size: $font-size-16; 
        font-weight: 700;
    }
    
    .rating {
        font-size: 0.7rem
    }
    
    .fa-star,
    .reviews {
        color: #daa520
    }
    
    .price {
        font-size: $font-size-24;
        color: $black;
        font-family: $font-noto;
        margin: 0
    }
    
    .card {
        position: relative
    }
    
    .card:hover {
        border: $border-default;
        .img-wrapper {
            .quick-view {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    
    .badge {
        background-color: #e03e3e;
        position: absolute;
        color: #fff;
        border-radius: 5px;
        right: 5px;
        top: 5px;
        text-transform: uppercase;
        z-index: 2;
    }
    
    a:hover {
        text-decoration: none
    }

    .img-wrapper {
        position: relative;
    }
    .quick-view {
        @include centered-vertical();
        width: 100%;
        padding: 10px 5px;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        background-color: rgba(196, 136, 12, 0.85);
        color: $white;
        @include textshadow(1px, 0px, 2px, rgba(0,0,0,0.25));
        transition: 0.2s;

        opacity: 0;
        visibility: hidden;
        &:hover {
            background-color: rgba(0, 0, 0, 0.90);
            color: $yellow;
        }
    }
}

.product-item {
    .product-name {
        a {
            color: $black;
            text-decoration: none;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    .category {
        text-decoration: none;
        text-transform: uppercase;
        color: $black;
        &:hover {
            text-decoration: underline;
        }
    }
}

.btn-circle {
    width: 30px;
    height: 30px;
    padding: 6px 0px;
    border-radius: 15px;
    text-align: center;
    font-size: 12px;
    line-height: 1.42857;
}

.btn-cart {
    background-color: $yellow;
    border-color: $yellow;
    &:hover {
        color: $yellow;
    }
} 

.social-links {
    a, .item {
        position: relative;
        display: inline-block;
        width: 36px;
        height: 36px;
        padding: 8px 0;
        margin-right: 4px;
        line-height: 1;
        text-align: center;
        background: $dgray;
        color: #fff;
        font-size: $font-size-24;
        border-radius: 50%;
        transition: 0.3s;
        i, .icon {
            @include centered-full();
        }
        &:hover {
            background: $dyellow;
            color: #fff;
            text-decoration: none;
        }
    }
    .facebook {
        background-color: $facebook;
    }
    .youtube {
        background-color: $youtube;
    }
    .instagram {
        background-color: $instagram;
    }
    .twitter {
        background-color: $twitter;
    }
}
 
.breadcrumb {
    padding: 20px 0 10px 0;
}

.card-categories {
    .card-body {
        margin: 0;
        padding-bottom: 0.3rem;
    }
    .list-group {
        margin: 0 1rem;
        padding: 10px 0;
    }
    .list-group-item {
        padding: .5rem 1.2rem;
        border: 0;
        margin: 3px 0;
        font-size: $font-size-18;
        .count {
            font-size: $font-size-12;
            color: $dgray;
        }
        &.active {
            .count {
                color: $white;
            }
        }
    }
}

.inner-product-container {
    
    .card {
        border: none
    }
    
    .product {
        background-color: #eee
    }
    
    .brand {
        font-size: 13px
    }
    
    .act-price {
        color: red;
        font-weight: 700
    }
    
    .dis-price {
        text-decoration: line-through
    }
    
    .about {
        font-size: 14px
    }
    
    .color {
        margin-bottom: 10px
    }
    
    label.radio {
        cursor: pointer
    }
    
    label.radio input {
        position: absolute;
        top: 0;
        left: 0;
        visibility: hidden;
        pointer-events: none
    }
    
    label.radio span {
        padding: 2px 9px;
        border: 2px solid $yellow;
        display: inline-block;
        color: $yellow;
        border-radius: 3px;
        text-transform: uppercase
    }
    
    label.radio input:checked+span {
        border-color: $yellow;
        background-color: $yellow;
        color: #fff
    }
    
    .btn-primary {
        background-color: $yellow !important;
        border-color: $yellow !important
    }
    
    .btn-primary:hover {
        background-color: $dyellow !important;
        border-color: $dyellow !important
    }
    
    .btn-primary:focus {
        box-shadow: none
    }
    
    .cart i {
        margin-right: 10px
    }

    .btn-addtocart {
        margin-right: 10px;
    }
}

.account-container {
    background-color: rgba(0, 0, 0, 0.05);
}


/* Footer */
.footer {
    background: #404040;
    padding: 0 0 30px 0;
    color: #fff;
    font-size: 14px;
    .footer-newsletter {
        padding: 50px 0;
        background: #404040;
        h4 {
            font-size: 24px;
            margin: 0 0 20px 0;
            padding: 0;
            line-height: 1;
            font-weight: 600;
        }
        form {
            margin-top: 30px;
            background: #fff;
            padding: 6px 10px;
            position: relative;
            border-radius: 50px;
            input[type="email"] {
                border: 0;
                padding: 8px;
                width: calc(100% - 140px);
            }
            input[type="submit"] {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                border: 0;
                background: none;
                font-size: 16px;
                padding: 0 30px;
                margin: 3px;
                background: $dyellow;
                color: #fff;
                transition: 0.3s;
                border-radius: 50px;
            }
        }
    }
    .footer-top {
        background: $black;
        border-top: 1px solid #474747;
        border-bottom: 1px solid #474747;
        padding: 60px 0 30px 0;
        .footer-info {
            margin-bottom: 30px;
            h3 {
                font-size: 18px;
                margin: 0 0 20px 0;
                padding: 2px 0 2px 0;
                line-height: 1;
                font-weight: 700;
            }
            p {
                font-size: 14px;
                line-height: 24px;
                margin-bottom: 0;
                font-family: "Raleway", sans-serif;
                color: #fff;
            }
        }
        h4 {
            font-size: 16px;
            font-weight: bold;
            color: #fff;
            text-transform: uppercase;
            position: relative;
            padding-bottom: 12px;
        }
        .footer-links {
            margin-bottom: 30px;
            ul {
                list-style: none;
                padding: 0;
                margin: 0;
                li {
                    padding: 10px 0;
                    display: flex;
                    align-items: center;
                    &:first-child {
                        padding-top: 0;
                    }
                }
                i {
                    padding-right: 2px;
                    color: $ddyellow;
                    font-size: 18px;
                    line-height: 1;
                }
                a {
                    color: #fff;
                    transition: 0.3s;
                    display: inline-block;
                    line-height: 1;
                    &:hover {
                        color: $yellow;
                    }
                }
            }
        }
        .footer-contact {
            margin-bottom: 30px;
            p {
                line-height: 26px;
            }
        }
        .email-link {
            color: $yellow;
        }
    }
    .copyright {
        text-align: center;
        padding-top: 30px;
    }
    .credits {
        padding-top: 10px;
        text-align: center;
        font-size: 13px;
        color: #fff;
    }
}

@media(max-width:800px) {
    .wrapper {
        margin: 20px 10px
    }

    p.h4 {
        font-size: 1.35rem;
        padding-top: 10px
    }
}

@media(max-width:767px) {
    .wrapper {
        max-width: 500px;
        margin: 20px auto
    }
}

@media(max-width:424px) {
    .wrapper {
        width: 100%;
        margin: auto
    }

    div.text-muted {
        font-size: 0.75rem
    }
}